"use client";

import { useEffect, useRef, useState, useCallback } from "react";

interface BrownNoisePlayerProps {
    isPlaying: boolean;
    volume?: number; // 0-1, Pro feature
    showVolumeControl?: boolean;
    onVolumeChange?: (volume: number) => void;
}

export function BrownNoisePlayer({
    isPlaying,
    volume = 0.5,
    showVolumeControl = false,
    onVolumeChange,
}: BrownNoisePlayerProps) {
    const audioRef = useRef<HTMLAudioElement | null>(null);
    const [isLoaded, setIsLoaded] = useState(false);

    // Initialize audio on mount
    useEffect(() => {
        audioRef.current = new Audio("/brown-noise.mp3");
        audioRef.current.loop = true;
        audioRef.current.volume = volume;

        audioRef.current.addEventListener("canplaythrough", () => {
            setIsLoaded(true);
        });

        return () => {
            if (audioRef.current) {
                audioRef.current.pause();
                audioRef.current = null;
            }
        };
    }, []);

    // Handle play/pause
    useEffect(() => {
        if (!audioRef.current) return;

        if (isPlaying && isLoaded) {
            audioRef.current.play().catch((err) => {
                console.warn("Audio play failed:", err);
            });
        } else {
            audioRef.current.pause();
        }
    }, [isPlaying, isLoaded]);

    // Handle volume changes
    useEffect(() => {
        if (audioRef.current) {
            audioRef.current.volume = volume;
        }
    }, [volume]);

    const handleVolumeChange = useCallback(
        (e: React.ChangeEvent<HTMLInputElement>) => {
            const newVolume = parseFloat(e.target.value);
            onVolumeChange?.(newVolume);
        },
        [onVolumeChange]
    );

    if (!showVolumeControl) {
        return null; // Just manages audio, no UI for free users
    }

    return (
        <div className="flex items-center gap-3">
            <svg className="w-4 h-4 text-surface-400" fill="currentColor" viewBox="0 0 24 24">
                <path d="M11.5 3.75a.75.75 0 00-1.24-.57L6.01 7H4a1 1 0 00-1 1v8a1 1 0 001 1h2.01l4.25 3.82a.75.75 0 001.24-.57V3.75z" />
                {volume > 0 && (
                    <path d="M14.5 7.5a.75.75 0 011.06 0 5.5 5.5 0 010 7.78.75.75 0 11-1.06-1.06 4 4 0 000-5.66.75.75 0 010-1.06z" />
                )}
                {volume > 0.5 && (
                    <path d="M17.5 4.5a.75.75 0 011.06 0 9.5 9.5 0 010 13.43.75.75 0 01-1.06-1.06 8 8 0 000-11.31.75.75 0 010-1.06z" />
                )}
            </svg>
            <input
                type="range"
                min="0"
                max="1"
                step="0.1"
                value={volume}
                onChange={handleVolumeChange}
                className="w-24 h-1 bg-surface-700 rounded-lg appearance-none cursor-pointer accent-primary-500"
            />
        </div>
    );
}
