"use client";

import { useState, useEffect, useCallback } from "react";
import { useRouter } from "next/navigation";
import { Timer } from "@/components/Timer";
import { ProgressRing } from "@/components/ProgressRing";
import { QuitModal } from "@/components/QuitModal";
import { BrownNoisePlayer } from "@/components/BrownNoisePlayer";
import { Button } from "@/components/ui/Button";
import { useTimer } from "@/hooks/useTimer";
import { useFullscreen } from "@/hooks/useFullscreen";
import {
    getActiveSession,
    clearActiveSession,
    incrementQuitAttempts,
    addToHistory,
    getPreferences,
    setPreferences,
} from "@/lib/storage";

export default function FocusPage() {
    const router = useRouter();
    const { seconds, isComplete, progress } = useTimer();
    const { enterFullscreen, exitFullscreen, isSupported } = useFullscreen();

    const [showQuitModal, setShowQuitModal] = useState(false);
    const [brownNoise, setBrownNoise] = useState(false);
    const [brownNoiseVolume, setBrownNoiseVolume] = useState(0.5);
    const [isPro, setIsPro] = useState(false); // Will be connected to Clerk later
    const [mounted, setMounted] = useState(false);
    const [sessionDuration, setSessionDuration] = useState(0);

    // Initialize and load session
    useEffect(() => {
        setMounted(true);
        const session = getActiveSession();

        if (!session) {
            // No active session, redirect to home
            router.push("/");
            return;
        }

        setBrownNoise(session.brownNoise);
        setSessionDuration(session.duration);

        const prefs = getPreferences();
        setBrownNoiseVolume(prefs.brownNoiseVolume || 0.5);

        // Try to enter fullscreen after a short delay
        if (isSupported) {
            const timer = setTimeout(() => {
                enterFullscreen();
            }, 500);
            return () => clearTimeout(timer);
        }
    }, [router, enterFullscreen, isSupported]);

    // Handle session completion
    useEffect(() => {
        if (isComplete && mounted) {
            // Add to history
            addToHistory({
                date: new Date().toISOString().split("T")[0],
                duration: sessionDuration,
                completed: true,
            });

            clearActiveSession();
            exitFullscreen();
            router.push("/complete");
        }
    }, [isComplete, mounted, sessionDuration, exitFullscreen, router]);

    const handleQuitClick = useCallback(() => {
        incrementQuitAttempts();
        setShowQuitModal(true);
    }, []);

    const handleQuitConfirm = useCallback(() => {
        // Add incomplete session to history
        addToHistory({
            date: new Date().toISOString().split("T")[0],
            duration: sessionDuration,
            completed: false,
        });

        clearActiveSession();
        exitFullscreen();
        router.push("/");
    }, [sessionDuration, exitFullscreen, router]);

    const handleVolumeChange = useCallback((volume: number) => {
        setBrownNoiseVolume(volume);
        setPreferences({ brownNoiseVolume: volume });
    }, []);

    // Loading state
    if (!mounted) {
        return (
            <div className="focus-mode">
                <div className="w-8 h-8 border-2 border-primary-500 border-t-transparent rounded-full animate-spin" />
            </div>
        );
    }

    return (
        <div className="focus-mode">
            {/* Brown noise audio player */}
            <BrownNoisePlayer
                isPlaying={brownNoise}
                volume={brownNoiseVolume}
                showVolumeControl={isPro}
                onVolumeChange={handleVolumeChange}
            />

            {/* Main content */}
            <div className="relative flex flex-col items-center justify-center gap-8 animate-fade-in">
                {/* Progress ring with timer inside */}
                <div className="relative">
                    <ProgressRing progress={progress} size={320} strokeWidth={4} />
                    <div className="absolute inset-0 flex flex-col items-center justify-center">
                        <Timer seconds={seconds} />
                    </div>
                </div>

                {/* Motivational text */}
                <p className="text-surface-400 text-lg md:text-xl text-center max-w-md breathing">
                    Stay locked. Finish what you started.
                </p>

                {/* Brown noise indicator */}
                {brownNoise && (
                    <div className="flex items-center gap-2 text-surface-500 text-sm">
                        <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 24 24">
                            <path d="M11.5 3.75a.75.75 0 00-1.24-.57L6.01 7H4a1 1 0 00-1 1v8a1 1 0 001 1h2.01l4.25 3.82a.75.75 0 001.24-.57V3.75z" />
                            <path d="M14.5 7.5a.75.75 0 011.06 0 5.5 5.5 0 010 7.78.75.75 0 11-1.06-1.06 4 4 0 000-5.66.75.75 0 010-1.06z" />
                        </svg>
                        <span>Brown noise playing</span>
                        {isPro && (
                            <BrownNoisePlayer
                                isPlaying={false}
                                volume={brownNoiseVolume}
                                showVolumeControl={true}
                                onVolumeChange={handleVolumeChange}
                            />
                        )}
                    </div>
                )}
            </div>

            {/* Quit button - de-emphasized */}
            <div className="absolute bottom-8 left-1/2 -translate-x-1/2">
                <Button
                    variant="ghost"
                    size="sm"
                    onClick={handleQuitClick}
                    className="text-surface-600 hover:text-surface-400"
                >
                    Quit session
                </Button>
            </div>

            {/* Quit confirmation modal */}
            <QuitModal
                isOpen={showQuitModal}
                onClose={() => setShowQuitModal(false)}
                onConfirmQuit={handleQuitConfirm}
                isPro={isPro}
            />
        </div>
    );
}
